@import "../../../../styles/color"
.text
  font-weight: 500
  margin-right: 25px
  color: $colorMain
  font-size: 16px

.activeLink
  color: $colorMain

nav
  position: relative

nav .text
  position: relative

nav .text::before
  content: ""
  position: absolute
  bottom: 0
  left: 0
  width: 0
  margin-bottom: -2px
  height: 1px
  background-color: $colorMain
  transition: width 0.3s ease-in-out


nav .text:hover::before,
nav .activeLink::before
  width: 100%

.add
  float: right
  background: $colorCheckbox
  color: white
  border-radius: 50%
  width: 30px
  text-align: center
  font-size: 25px

.main
  display: flex
  flex-direction: row
  justify-content: space-between